<template>
  <div class="table1">
    <div class="add cb">
      <div class="add-left fl">个人周报</div>
    </div>
    <el-table ref="multipleTable" :data="weeklyList" tooltip-effect="dark" type="index" style="width: 100%">
      <el-table-column type="selection" width="75">
      </el-table-column>
      <el-table-column prop="subjectName" label="周报名称" show-overflow-tooltip>
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row.subjectName}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="keyWord" label="关键字标签" width="170">
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row.keyWord}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="subjectGroupTypeValue" label="归属组"  width="130">
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row.subjectGroupTypeValue}}</div>
        </template>
      </el-table-column>
      <el-table-column prop="createDate" label="发布时间" width="200">
        <template scope="scope">
          <div @click="knowledgeOper(scope.row)">{{scope.row.createDate}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="180">
        <template scope="scope">
            <el-button type="text" @click="deletelist(weeklyList.weeklyId,scope.$index)"><i class="el-icon-delete delete"></i></el-button>
            <el-button type="text" @click="edit(scope.row)" ><i class="el-icon-edit"></i></el-button>
        </template>
			</el-table-column>
		</el-table>
    <div class="hello">      
      <el-pagination  @size-change="pageSizeChange" @current-change="currentPageChange" :current-page="currentPage" :page-sizes="[ 5,10 ]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination> 
    </div>
  </div>
</template>

<script>
import service from "../scripts/service.js";
export default {
  name: "table1",
  data() {
    return {
      addDetail: {},
      editDetail: {},
      index: 0,
      weeklyList: [],
      subjectName: "",
      staffname: "",
      staffID: "",
      editid: "",
      //搜索表单
      searchForm: {
        id: "",
        name: "",
        state: ""
      },
      //多选值
      multipleSelection: [],
      //当前页
      currentPage: 1,
      //分页大小
      pageSize: 10,
      //总记录数
      total: ""
    };
  },
  mounted: function() {
    this.getAauthor();
  },
  methods: {
    getAauthor() {
      service
        .getListSxByGType()
        .then(res => {
          console.log(res);
          this.Aauthor = res.data[4];
          console.log(this.Aauthor);
          this.staffname = this.Aauthor.staffname;
          this.staffID = this.Aauthor.staffId;
          let date = new Date();
          let seperator1 = "-";
          let year = date.getFullYear();
          let month = date.getMonth() + 1;
          let strDate = date.getDate();
          if (month >= 1 && month <= 9) {
            month = "0" + month;
          }
          if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
          }
          let currentdate = year + seperator1 + month + seperator1 + strDate;
          console.log(currentdate);
          this.subjectName =
            "IOD-个人实习周报-" + this.staffname + "-" + currentdate;
          console.log(this.subjectName);
          this.getList();
        })
        .catch(err => {
          console.log(err);
        });
    },
    getList() {
      let params = {
        pages: this.currentPage,
        pageSize: this.pageSize,
        staffId: this.staffID
      };
      console.log(params);
      service
        .getMyselfListWeely(params)
        .then(res => {
          console.log(res);
          // 将获取回来的数据赋值给list
          this.weeklyList = res.data.list;
          this.total = res.data.total;
          console.log(this.weeklyList);
        })
        .catch(err => {
          console.log(err);
        });
    },
    //删除
    deletelist(weeklyId, index) {
      this.$confirm("确认删除？")
        .then(_ => {
          let params = {
            weeklyId: this.weeklyList[index].weeklyId
          };
          console.log(params);
          service
            .delWeeklyInfo(params)
            .then(response => {
              console.log(response);
              // 加载列表
              this.getAauthor();
            })
            .catch(error => {
              console.log(error);
              alert("删除失败！");
            });
        })
        .catch(_ => {});
    },
    //编辑
    edit(row) {
      console.log(this.staffID);
      let routeData = this.$router.resolve({
        path: "/weeklyModify",
        query: {
          id: row.weeklyId,
          subjectName: this.subjectName,
          staffname: this.staffname,
          staffID: this.staffID
        }
      });
      window.open(routeData.href, "_blank");
    },
    //分页大小修改事件
    pageSizeChange: function(val) {
      console.log("每页 " + val + " 条");
      this.pageSize = val;
      this.getList();
    },
    //当前页修改事件
    currentPageChange: function(val) {
      this.currentPage = val;
      console.log("当前页: " + val);
      this.getList();
    },
    knowledgeOper(row) {
      console.log(this.staffID);

      let routeData = this.$router.resolve({
        path: "/weeklyDetail",
        query: {
          id: row.weeklyId
        }
      });
      window.open(routeData.href, "_blank");
    }
  }
};
</script>

<style scoped>
table {
  width: 100%;
  font-size: 14px;
  border: 1px solid #eee;
}
table thead th {
  background: #e5eff8;
  padding: 22px 18px 22px 18px;
  text-align: center;
}
table tbody td {
  padding: 10px 0px 10px 18px;
  text-align: left;
  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
}
table span {
  margin: 0 10px;
  cursor: pointer;
}
.delete {
  color: red;
}
.edit {
  color: #008cd5;
}
.add {
  border: 1px solid #eee;
  margin-bottom: 10px;
  height: 65px;
  padding: 15px 0px 15px 15px;
}
tbody tr:hover {
  background-color: #e5f0f86c;
}
.add button {
  background: #008cd5;
  border: 0;
  cursor: pointer;
  padding: 6px 15px;
  border-radius: 3px;
  color: #fff;
  margin-left: 10px;
}
.hello {
  margin-top: -10px;
  margin-bottom: 10px;
}
.header-search-input {
  display: inline-block;
  right: 1%;
  margin-left: 12px;
  width: 300px;
}
.header-search-input input {
  position: relative;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #d8dce5;
  display: inline-block;
  height: 35px;
  line-height: 1;
  outline: 0;
  padding: 0 25px 0 15px;
  width: 280px;
  margin-right: -4px;
}
.query-search-btn {
  color: #c2d6e7;
  position: relative;
  display: inline-block;
  margin-left: -30px;
  cursor: pointer;
}
.add-left {
  font-size: 16px;
}
.hello {
  position: fixed;
  bottom: 0px;
  right: 20px;
}
</style>



